/* eslint-disable react/sort-comp */
import React, { Fragment, Component } from 'react';
import Types from 'prop-types';
import { Radio } from '..';

class RadioGroup extends Component {
  static propTypes = {
    radios: Types.arrayOf(
      Types.shape({
        id: Types.string,
        label: Types.string.isRequired,
        name: Types.string.isRequired,
        checked: Types.bool,
        disabled: Types.bool,
      }),
    ).isRequired,
    onChange: Types.func.isRequired,
  };

  getChecked = () => {
    const { radios } = this.props;
    let checkedRadios = 0;

    radios.some((radio, i) => {
      checkedRadios = i;
      return radio.checked;
    });

    return checkedRadios;
  };

  state = { checked: this.getChecked() > 0 ? this.getChecked() : 0 };

  componentDidMount() {
    const { radios } = this.props;
    try {
      if (!radios || radios.length < 2) {
        throw new Error('At least two radio options should be provided');
      }
    } catch (e) {
      // eslint-disable-next-line
      console.log(e.message);
    }
  }

  handleOnChange = checked => {
    const { onChange } = this.props;
    this.setState({ checked }, onChange && onChange(checked));
  };

  render() {
    const { checked } = this.state;
    const { radios } = this.props;
    return radios.length > 1 ? (
      <Fragment>
        {radios.map(({ id, label, name, disabled }, index) => (
          <Radio
            id={id || `radio-${index}`}
            // eslint-disable-next-line react/no-array-index-key
            key={index}
            label={label}
            name={name}
            checked={checked === index}
            disabled={disabled}
            onChange={() => this.handleOnChange(index)}
          />
        ))}
      </Fragment>
    ) : null;
  }
}

export default RadioGroup;



