import React, { Component } from 'react';
import classNames from 'classnames';
import { RadioGroup } from '..';

const RADIOS = [
  { label: 'Radio1', name: 'name', checked: false, disabled: false },
  { label: 'Radio2', name: 'name', checked: false, disabled: false },
  { label: 'Radio3', name: 'name', checked: true, disabled: false },
];

export default class RadioGroupDocs extends Component {
  getDocs = () => (
    <pre className="tw-docs-code ">
      {`<RadioGroup 
 radios={[options]} 
 onChange={this.handleOnChange}
/>`}
    </pre>
  );

  handleOnChange = value => {
    // eslint-disable-next-line
    console.log('Value selected', value);
  };

  render() {
    return (
      <div className="container">
        <section className="section">
          {/* Full Component */}
          <div className="row">
            <div className="col-md-6">
              <h2>Radio group</h2>
              <span role="img" aria-label="uhm">
                Radio killed the checkbox star
              </span>
              {getExtraDocs()}
              {this.getDocs()}
            </div>
            <div className="col-md-6">
              <div
                className={classNames({
                  ' m-b-4': true,
                })}
              >
                <RadioGroup radios={RADIOS} onChange={this.handleOnChange} />
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const getExtraDocs = () => (
  <div>
    <p className=" m-t-5">
      This component render a simple group of radio options, and broadcast the index of selected
      option <code>onChange</code>
    </p>
  </div>
);



